@import "../utils/variables";
// jsgrid css
.jsgrid-filter-row {
    select, input {
        padding: 6px 10px;
        border: 1px solid $horizontal-border-color;
        background: $white;
    }
}

// popover
.popover {
    background-color: $white;
    border: none;
    -webkit-box-shadow: 0 0 20px rgba($primary-color, 0.1);
    box-shadow: 0 0 20px rgba($primary-color, 0.1);
    .popover-header {
        background-color: $theme-medium-color;
        color: $primary-color;
        border-bottom: none;
    }
    .popover-body {
        color: rgba(43,43,43,0.7);
    }
}

// tooltip css start
.tooltip {
    &.bs-tooltip-top {
      .tooltip-arrow {
          &:before {
              border-top-color:$theme-medium-color;
          }
      }
    }
    &.bs-tooltip-bottom {
        .tooltip-arrow {
            &:before {
                border-bottom-color: $theme-medium-color;
                border-top-color: transparent;
            }
        }
    }
    &.bs-tooltip-start {
        .tooltip-arrow {
            &:before {
                border-left-color: $theme-medium-color;
                border-top-color: transparent;
            }
        }
    }
    &.bs-tooltip-end {
        .tooltip-arrow {
            &:before {
                border-right-color: $theme-medium-color;
                border-top-color: transparent;
            }
        }
    }
    .tooltip-inner {
        background-color: $theme-medium-color;
        color: $primary-color;
    }
    .tooltip-arrow {
        &:before {
            border-top-color: $theme-medium-color;
        }
    }
}

// tooltip css end

// dropdown css start
.dropdown-basic {
    .dropdown {
        .dropdown-content {
            a {
                padding: 6px 16px;
                color: $dark-editor-document;
                opacity: 0.6;
                font-size: 13px;
                border-top: 1px solid $light-semi-gray;
                background: $white;
                &:hover {
                    background-color: $white;
                }
            }
            .dropdown-header {
                padding: 8px 16px;
                font-weight: 400;
                color: $dark-color;
                font-size: 13px;
            }
        }
    }
    .dropup {
        .dropup-content {
            top: auto;
        }
    }
}
// dropdown css end
// accordian css start
.default-according {
    .card {
        .card-header {
            padding: 0;
            border: none;
            border-radius: 0;
            border-top-left-radius: 5px;
            border-top-right-radius: 5px;
            &.bg-primary,&.bg-secondary {
                .btn {
                    border-color: $transparent-color;
                }
            }
            i {
                position: initial;
                font-size: 20px;
                display: inline-block;
                vertical-align: text-bottom;
                margin-right: 5px;
                [dir="rtl"] & {
                    margin-right: unset;
                    margin-left: 5px;
                }
            }
            h5 {
                margin-top: 0;
            }
            .btn-link {
                padding: 12px 20px;
                width: 100%;
                text-align: left;
                letter-spacing: 0.7px;
                font-family: Roboto;
                border: 1px solid $light-semi-gray;
                border-radius: 0;
                border-top-left-radius: 5px;
                border-top-right-radius: 5px;
                [dir="rtl"] & {
                    text-align: right;
                }
                @media (max-width: 575px) {
                    padding: 12px 15px;
                }
            }
        }
        .card-body {
            padding: 15px 20px;
            line-height: 22px;
            font-size: 13px;
            border-color: $light-semi-gray;
            border-radius: 0;
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
            color: $dark-color;
        }
    }
} 
// accordian css end

// tab-bootstrap css start
.nav-tabs {
    border-bottom-color: $light-semi-gray;
    .nav-bottom {
        .nav-item {
            .nav-link.active {
                border-color: $light-semi-gray;
            }
        }
    } 
}
.nav-tabs {
    .nav-item{
        &.show {
            .nav-link {
                font-weight: 500;
            }
        }
    }
    .nav-link {
        color: $dark-color;
        &.active {
            font-weight: 500;
        }
    }
    &.nav-bottom {
        .nav-item {
            .nav-link {
                &.active {
                    border-color: $light-gray $light-gray #fff;
                }
            }
        }
    }
}
.dropdown-menu {
    -webkit-box-shadow: 0 0 20px rgba(89,102,122,0.1);
    box-shadow: 0 0 20px rgba(89,102,122,0.1);
    border: none;
    padding: 0;
    z-index: 1;
    .dropdown-item {
      color: #2b2b2b;
      opacity: 0.6;
      font-size: 13px;
      padding: 6px 12px;
      border-top: 1px solid $light-semi-gray;
      background: $white;
      &:hover {
        background-color: $white;
      }
    }
    .dropdown-divider {
        margin: 0;
        border-top: 1px solid #cccccc;
    }
  }
// tab-bootstrap css end

.border-tab{
    .nav-tabs {
        .nav-item {
            .nav-link {
                &.active {
                    border-bottom: 2px solid $primary-color;
                }
                &.show {
                    border-bottom: 2px solid $primary-color;
                }
                &:focus {
                    border-bottom: 2px solid $primary-color;
                }
            }
        }
    }
}
.tabbed-card {
    .nav-tabs {
        .nav-item {
            .nav-link {
                top: 17px;
            }
        }
    }
}
.timeliny {
    .timeliny-dot::before {
        font-size: $btn-lg-font-size;
    }
}
.form-builder-header-1,.form-builder-2-header {
    background-color: #f7f6ff;
    .nav-primary {
        .nav-link {
            color: $dark-color;
            font-weight: 400;
            &.active {
                color: $white;
            }
        }
    }
    h6 {
        margin-bottom: 0;
        font-weight: 400;
        line-height: 36px;
    }
}
.component {
    .input-group {
        .btn {
            line-height: 32px;
            text-transform: capitalize;
        }
    }
}
.form-builder {
    .drag-box {
        fieldset {
            margin-top: 0;
        }
    }
}
.login-card {
    .login-main {
        .theme-form {
            label {
                font-size: $body-font-size;
            }
            .show-hide {
                top: 50%;
            }
        }
    }
}
// faq css start
.faq-accordion {
    .card {
        .btn-link {
            svg {
                margin-left: 20px;
                @media (max-width: 575.98px) {
                    margin-left: 10px;
                }
            }
        }
    }
}
// faq css end
.job-filter {
    .faq-form {
        .form-control {
            font-size: 14px;
        }
        .search-icon {
            width: 16px;
            height: 16px;
        }
    }
}
// editor css
button[data-toggle="dropdown"] {
   &:hover {
     ~ {
        .dropdown-menu {
            display: block;
            min-width: 300px;
            top: 35px;
        }
    }
   }
}

// vue pluging scss
.vue-feather {
    vertical-align: -3px;
}
.dp__input{
    border: none;
}
.form-control-plaintext{
    color: $gray-900 !important;
}
.form-control{
    color: $gray-900 !important;
}
.fc{
    .fc-scrollgrid-section-body{
        table{
            border-bottom-style: unset;
        }
    }
    .fc-scrollgrid{
        table{
            border-right-style: unset;
        }
    }
}
.tag-pills{
    .badge{
        svg{
            padding-top: 0;
        }
    }
}
.static-modal{
    .modal-content{
      border: 1px solid $popover-border-color;
    }
    .modal-header {
      padding: 1rem;
      border-bottom: 1px solid $gray-300;
      border-top-left-radius: calc(0.3rem - 1px);
      border-top-right-radius: calc(0.3rem - 1px);
    }
    .modal-body {
      padding: 1rem;
    }
    .modal-footer {
      padding: 0.75rem;
      border-top: 1px solid $gray-300;
      border-bottom-right-radius: calc(0.3rem - 1px);
      border-bottom-left-radius: calc(0.3rem - 1px);
      flex-wrap: wrap;
    }
}
.tree-row-txt{
    margin-left: 5px;
    [dir="rtl"] & {
        margin-left: unset;
        margin-right: 5PX;
    }
}
.rating-container{
    .rating-pre {
        padding: 20px 10px;
    }
    .vue3-star-ratings__wrapper{
        .vue3-star-ratings{
            button{
                width: 32px !important;
                height: 32px !important;
            }
        }
    }
}
.swal2-styled.swal2-cancel{
    color: $black !important;
}
.owl-theme{
    .swiper{
        .swiper-pagination{
            span{
                background-color: $primary-color;
            }
        }
    }
}
.vue-slider-process{
    background-color: $primary-color;
}
.vue-slider-dot-handle{
    border-color: $primary-color;
}
.vue-slider-mark-step-active{
    box-shadow: 0 0 0 2px $primary-color;
}
.vue-slider:hover{
    .vue-slider-process {
        background-color: $primary-color;
    }
    .vue-slider-dot-handle {
        border-color: $primary-color;
    }
    .vue-slider-mark-step-active{
        box-shadow: 0 0 0 2px $primary-color;
    }
}
.vue-slider-dot-tooltip-inner{
    font-size: 12px;
    padding: 4px 5px;
} 
.vue-slider-dot{
    z-index: 3;
}
.tilt-showcase{
    code{
        text-shadow: none;
    }
}